<template>
	<div class="intro-wrap">

		<div class="admin-bar">
			<span><i class="fa fa-users" @click ="toggle(1)"></i></span>
			<span><i class="fa fa-flag" @click ="toggle(0)"></i></span>
		</div>
		<hr>
		<component :is="view"></component>
	</div>
</template>

<script type="text/ecmascript-6">
	import Photo from './Photo.vue'
	import Socialrelations from './Socialrelations.vue'
	import Editperson from './Editperson.vue'
    import Editact from './Editact.vue'
	import Afteract from './Afteract.vue'
	import Editafter from './Editafter.vue'
	import userAdmin from './userAdmin.vue'
	import actAdmin from './actAdmin.vue'

	export default {
		components : {
			userAdmin,
			actAdmin
		},
		mounted : function() {
		},
		computed: {
		},
		data () {
			return {
                view:actAdmin,
			}
		},
		methods : {
			toggle :function (status) {
				if(status){
				    this.view = userAdmin;
				}else{
				    this.view = actAdmin;
				}
            }
		}
	}
</script>

<style>
	@import url("../css/font-awesome.min.css");
	@import url("../css/introduction.css");
	.admin-bar span{
		cursor: pointer;
		font-weight: bold;
		font-size: 30px;
		color: #1C93D6;
	}
	.admin-bar span:nth-child(2){
		color: #4DB9A3;
	}
	.act-table-head{
		display: flex;
	}
	.act-table-head span{
		flex:1;
	}
	.act-table-head span:nth-child(4){
		flex:2;
	}
	.act-table-head span:nth-child(5){
		flex:2;
	}
	.act-table-body ul li{
		display: flex;
		list-style: none;
		margin: 5px 0px;
		align-items: center;
	}
	.act-table-body span{
		flex:1;
	}
	.act-table-body span:last-child{
		cursor: pointer;
	}
	.act-table-body span:last-child i{
		font-weight: bold;
		font-size:18px;
	}
	.act-table-body .act-dec{
		flex: 2;
		overflow: hidden;
		text-overflow:ellipsis;
		white-space: nowrap;
		color: #6BAFE1;
	}
	.act-table-body span:nth-child(5){
		flex:2;
	}
	.act-dec img{
		vertical-align: middle;
	}
	.greenclass{
		color: #42b983;
	}
	.blueclass{
		color: #6BAFE1;
	}
	.redclass{
		color: #FF3757;
	}
	.orangeclass{
		color: coral;
	}


</style>


