<template>
    <div class="page">
        <div class="container">
            <div class="title">
                <h2>Complain</h2>
            </div>
            <div class="wrap" @keyup.enter="complainIn">
                <input type="text" placeholder="" v-model="actName">
                <input type="text" placeholder="" v-model="telephone">
                <textarea name="dec" placeholder="" v-model="dec"></textarea>
                <p class="right" @click="back">back?</p>
                <button @click="complainIn"><i class="fa fa-registered"></i> COMPLAIN</button>
            </div>
        </div>
    </div>
</template>

<script>
    import { mapState } from 'vuex'
    import { mapGetters } from 'vuex'

    export default {
        data () {
            return {
                actName : '',
                dec : '',
                telephone:'',
                username:this.$route.params.introductionId
            }
        },
        computed :
            mapGetters([
                'doneTodos',
                'doneTodosLength'
            ]),
        methods: {
            complainIn : function() {
                var _this = this;
                var param = {name : this.name, pwd : this.pwd};
                var resource = this.$resource('http://localhost:3000/judgeLoading');
                resource.save(param).then((response) => {
                    if(response.body.code == 200) {
                        console.log('200');
                        sessionStorage.setItem('name', _this.name);
                        this.$router.push('/gallery/' + _this.name);
                    }
                    else {
                        alert(response.body.description);
                        _this.name = '';
                        _this.pwd = '';
                    }
                })
                    .catch(function(response) {
                        console.log('err' + response)
                    })
            },
            visitorIn : function() {
                console.log('');
                console.log(this.doneTodos);
            },
            createId : function() {
                console.log('');
//				this.$store.commit('increment');
            },
            back : function () {
                this.$router.push('/gallery/');
            }
        }
    }
</script>

<style>
    @import url("../css/font-awesome.min.css");
    @import url("../css/login.css");
</style>


