<template>
    <div>
        <div class="act-table-head">
            <span></span>
            <span></span>
            <span></span>
            <span></span>
            <span></span>
            <span></span>
            <span></span>
        </div>
        <div class="act-table-body">
            <ul>
                <li v-for="(preActOne, index) in preAct">
                    <span>{{}}</span>
                    <span>{{}}</span>
                    <span>{{}}</span>
                    <span>{{}}</span>
                    <span>{{}}</span>
                    <span>{{}}</span>
                    <span><i class="fa fa-trash redclass" @click=""></i></span>
                </li>
            </ul>
        </div>
    </div>
</template>
<script type="text/ecmascript-6">
    export default {
        created : function() {
            this.start();
        },
        data () {
            return {
                showDetail : false,
                showPay:false,
                detailactivity : {},
                detailpay1:'',
                detailpay2:'',
                width : this.imgWidth || '400px',
                height : this.imgHeight || '273px',
                activityMes : [
                ],
                actStatus:''
            };
        },
        methods : {
            start : function() {
                var _this = this;
                var params = { name : '' };
                if(this.$route.params.introductionId != undefined) {
                    params.name = this.$route.params.introductionId;
                }
                var resource = this.$resource('http://localhost:3000/getactivity');
                resource.save(params).then((response) => {
                    console.log(response.body);
                    _this.activityMes = response.body.activity;
                    for(var i in _this.activityMes) {
                        _this.activityMes[i].address = '/src/assets/act' + _this.activityMes[i].id + '.jpg';
//                        _this.activityMes[i].like = false;
                        _this.activityMes[i].activityLike = Number(_this.activityMes[i].likeNum);
                        _this.activityMes[i].activityComment = Number(_this.activityMes[i].activityComment);
                        var acttime = _this.activityMes[i].time;
                        _this.activityMes[i].actStatus = _this.checkStatus(acttime);

                    }
                })
                    .catch((response) => {
                        console.log('err ' + response);
                    })
            }
        }
    }
</script>
<style>
    .act-table-head{
        display: flex;
    }
    .act-table-head span{
        flex:1;
    }
    .act-table-head span:nth-child(4){
        flex:2;
    }
    .act-table-head span:nth-child(5){
        flex:2;
    }
    .act-table-body ul li{
        display: flex;
        list-style: none;
        margin: 5px 0px;
        align-items: center;
    }
    .act-table-body span{
        flex:1;
    }
    .act-table-body span:last-child{
        cursor: pointer;
    }
    .act-table-body span:last-child i{
        font-weight: bold;
        font-size:18px;
    }
    .act-table-body .act-dec{
        flex: 2;
        overflow: hidden;
        text-overflow:ellipsis;
        white-space: nowrap;
        color: #6BAFE1;
    }
    .act-table-body span:nth-child(5){
        flex:2;
    }
    .act-dec img{
        vertical-align: middle;
    }
    .redclass{
        color: red;
    }
</style>


